/* ÂØºËà™ÁªÑ‰ª∂Ê†∑Âºè */

.navigation {
  position: fixed; /* Âõ∫ÂÆöÂú®È°µÈù¢È°∂ÈÉ® */
  top: 0;
  left: 0;
  right: 0;
  background: rgba(26, 26, 26, 0.95);
  backdrop-filter: blur(10px);
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.4);
  z-index: 1000;
  transition: all 0.3s ease;
  border-bottom: 1px solid rgba(255, 215, 0, 0.2);
}

.nav-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 70px;
}

/* LogoÊ†∑Âºè */
.nav-logo .logo-link {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 20px;
  font-weight: bold;
  color: #ffd700;
  text-shadow: 0 2px 8px rgba(255, 215, 0, 0.3);
}

.logo-icon {
  font-size: 28px;
}

/* ÂØºËà™ËèúÂçïÂü∫Á°ÄÊ†∑Âºè */
.nav-menu {
  display: flex;
  gap: 10px;
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav-item {
  position: relative;
}

.nav-link {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 20px;
  border-radius: 25px;
  color: #ffffff;
  font-weight: 500;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.nav-link:hover {
  background: rgba(255, 215, 0, 0.1);
  color: #ffd700;
  transform: translateY(-2px);
}

/* ÂΩìÂâçÈ°µÈù¢ÁöÑÊøÄÊ¥ªÁä∂ÊÄÅ */
.nav-link.active {
  background: linear-gradient(45deg, #1a1a1a, #2a2a2a);
  color: #ffd700;
  box-shadow: 0 5px 15px rgba(255, 215, 0, 0.2);
  border: 1px solid rgba(255, 215, 0, 0.3);
}

.nav-icon {
  font-size: 18px;
}

/* Ê°åÈù¢Á´ØËèúÂçï - ÈªòËÆ§ÊòæÁ§∫ */
.desktop-menu {
  display: flex;
}

/* ÁßªÂä®Á´ØËèúÂçïÊåâÈíÆ - ÈªòËÆ§ÈöêËóè */
.mobile-menu-btn {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
}

.hamburger {
  width: 25px;
  height: 3px;
  background: #333;
  position: relative;
  transition: all 0.3s ease;
}

.hamburger::before,
.hamburger::after {
  content: '';
  position: absolute;
  width: 25px;
  height: 3px;
  background: #333;
  transition: all 0.3s ease;
}

.hamburger::before {
  top: -8px;
}

.hamburger::after {
  top: 8px;
}

/* Ê±âÂ†°ËèúÂçïÊøÄÊ¥ªÁä∂ÊÄÅ */
.hamburger.active {
  background: transparent;
}

.hamburger.active::before {
  top: 0;
  transform: rotate(45deg);
}

.hamburger.active::after {
  top: 0;
  transform: rotate(-45deg);
}

/* ÁßªÂä®Á´ØËèúÂçï - ÈªòËÆ§ÈöêËóè */
.mobile-menu {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.98);
  backdrop-filter: blur(10px);
  flex-direction: column;
  padding: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease, padding 0.3s ease;
}

.mobile-menu.open {
  max-height: 400px;
  padding: 20px;
}

.mobile-menu .nav-item {
  width: 100%;
}

.mobile-menu .nav-link {
  width: 100%;
  justify-content: center;
  padding: 15px;
  margin: 5px 0;
}

/* ÂìçÂ∫îÂºèËÆæËÆ° */
@media (max-width: 768px) {
  /* Ê°åÈù¢Á´ØËèúÂçïÈöêËóè */
  .desktop-menu {
    display: none;
  }
  
  /* ÁßªÂä®Á´ØËèúÂçïÊåâÈíÆÊòæÁ§∫ */
  .mobile-menu-btn {
    display: flex;
  }
  
  /* ÁßªÂä®Á´ØËèúÂçïÊòæÁ§∫ */
  .mobile-menu {
    display: flex;
  }
}

@media (max-width: 480px) {
  .nav-container {
    padding: 0 15px;
  }
  
  .nav-logo .logo-link {
    font-size: 18px;
  }
  
  .logo-icon {
    font-size: 24px;
  }
}