/* ÂõæÁâáÂç°Áâá‰ºòÂåñÊ†∑Âºè - ÈªëÈáëÈ´òÁ∫ß‰∏ªÈ¢ò */
.image-card-container {
  position: relative;
  width: 100%;
  height: 100%;
  /* ‰ΩøÁî®Áªü‰∏ÄÁöÑÂÆΩÈ´òÊØîÔºåÂü∫‰∫éÊúÄÂ§ßÂõæÁâáÂ∞∫ÂØ∏‰ºòÂåñ */
  /* SIX.png (723.21 KB) ‰Ωú‰∏∫Âü∫ÂáÜÔºåÈááÁî®Ê†áÂáÜÊµ∑Êä•ÊØî‰æã 2:3 */
  aspect-ratio: 2/3;
  max-height: 600px;
  overflow: hidden;
  background: #0a0a0a;
  border-radius: 12px;
  /* Á≤æËá¥ÁöÑËæπÊ°ÜÊïàÊûú */
  border: 1px solid rgba(255, 215, 0, 0.2);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
}

.image-card-img {
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
  /* ÈªòËÆ§Á°Æ‰øùÂõæÁâáÂÆåÊï¥ÊòæÁ§∫‰∏îÊØî‰æãÊ≠£Á°Æ */
  object-fit: contain;
  object-position: center;
  display: block;
  margin: auto;
  transition: opacity 0.3s ease, transform 0.3s ease;
  border-radius: 12px;
  /* È´òÁ∫ßÈªëËâ≤ËÉåÊôØ */
  background: #0a0a0a;
}

.image-card-img.loading {
  opacity: 0;
}

.image-card-img.loaded {
  opacity: 1;
}

.image-card-container:hover .image-card-img.loaded {
  transform: scale(1.02);
}

.image-card-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #ffd700;
  text-align: center;
  padding: 20px;
  background: #0a0a0a;
  border-radius: 12px;
}

.placeholder-icon {
  font-size: 3rem;
  margin-bottom: 10px;
}

.placeholder-text {
  font-size: 1rem;
  font-weight: 600;
  margin: 0;
  opacity: 0.9;
}

.image-card-loading {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 15px;
  z-index: 1;
}

.loading-spinner {
  width: 30px;
  height: 30px;
  border: 3px solid rgba(255, 255, 255, 0.3);
  border-top: 3px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Áªü‰∏ÄÈªëÈáë‰∏ªÈ¢òÔºåÈ´òÁ∫ßÊÑüËÆæËÆ° */
.image-card-container.adapted,
.image-card-container.not-adapted,
.image-card-container.in-preparation {
  background: #0a0a0a;
  border: 1px solid rgba(255, 215, 0, 0.3);
}

.image-card-container.adapted .image-card-placeholder,
.image-card-container.not-adapted .image-card-placeholder,
.image-card-container.in-preparation .image-card-placeholder {
  background: #0a0a0a;
}

/* ‰∏çÂêåÈ°µÈù¢ÂæÆÂ¶ôÁöÑËæπÊ°ÜÂ∑ÆÂºÇ */
.image-card-container.adapted {
  border-color: rgba(255, 215, 0, 0.4);
}

.image-card-container.not-adapted {
  border-color: rgba(255, 223, 0, 0.3);
}

.image-card-container.in-preparation {
  border-color: rgba(255, 193, 7, 0.2);
}

/* ÂìçÂ∫îÂºèËÆæËÆ° */
@media (max-width: 768px) {
  .image-card-container {
    aspect-ratio: 2/3;
    max-height: 400px;
  }
  
  .placeholder-icon {
    font-size: 2.5rem;
  }
  
  .placeholder-text {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .image-card-container {
    aspect-ratio: 2/3;
    max-height: 350px;
  }
  
  .placeholder-icon {
    font-size: 2rem;
  }
  
  .placeholder-text {
    font-size: 0.8rem;
  }
}